.mod-detail-container {
    display: flex;
    flex-direction: column;
    padding: 32px 0 0 0;
    max-width: 100vw;
    max-height: auto;
    min-height: 95vh;
    background: transparent;
}

.mod-detail-header {
    display: flex;
    align-items: center;
    background: #23233a;
    border-radius: 16px;
    padding: 24px 32px;
    margin: 0 24px 24px 24px;
    gap: 32px;
}

.mod-detail-img {
    width: 110px;
    height: 110px;
    border-radius: 16px;
    object-fit: cover;
    margin-right: 24px;
}

.mod-detail-title {
    font-size: 2.6em;
    font-weight: bold;
    margin: 0 0 8px 0;
    color: #fff;
}

.mod-detail-author {
    color: #14a0e1;
    font-weight: 600;
    margin-bottom: 8px;
    font-size: 1.1em;
}

.mod-detail-desc {
    color: #b3b8c5;
    font-size: 1.1em;
    margin-bottom: 8px;
}

.mod-detail-main {
    display: flex;
    flex-direction: row;
    gap: 32px;
    margin: 0 24px;
    align-items: flex-start;
}

.mod-detail-main-left {
    flex: 2;
    display: flex;
    flex-direction: column;
}

.mod-detail-tabs {
    display: flex;
    gap: 28px;
    margin-bottom: 0;
    margin-top: 18px;
    font-size: 1.1em;
    font-weight: 600;
    position: relative;
}

.mod-detail-tabs-underline {
    height: 2px;
    background: #23283a;
    width: 100%;
    margin-bottom: 18px;
    margin-top: -2px;
    position: relative;
    z-index: 1;
}

.mod-detail-tabs span {
    color: #b3b8c5;
    cursor: pointer;
    padding-bottom: 6px;
    border-bottom: 2px solid transparent;
    transition: color 0.15s, border-bottom 0.15s;
    position: relative;
    z-index: 2;
}

.mod-detail-tabs .active {
    color: #14a0e1;
    border-bottom: 2px solid #14a0e1;
}

.mod-detail-content {
    flex: 2;
    background: transparent;
    padding: 0 0 0 0;
}

.mod-detail-content h2 {
    font-size: 2em;
    margin-bottom: 8px;
    color: #fff;
}

.mod-detail-content p {
    color: #b3b8c5;
    font-size: 1.1em;
}

.mod-detail-sidebar {
    background: #23234a;
    border-radius: 16px;
    padding: 24px 24px 24px 24px;
    min-width: 300px;
    max-width: 340px;
    margin-left: 32px;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    box-shadow: 0 4px 24px #0004;
    gap: 5px;
}

.mod-detail-install-btn {
    width: 100%;
    font-size: 1.15em;
    margin-bottom: 10px;
    background: linear-gradient(90deg, #14e1a0 0%, #14a0e1 100%);
    color: #181c24;
    font-weight: bold;
    border-radius: 10px;
}

.mod-detail-actions-row {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
}

.mod-detail-download-btn,
.mod-detail-like-btn {
    flex: 1;
    background: #2e2e57;
    color: #fff;
    border-radius: 8px;
    font-size: 1.05em;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
}

.mod-detail-like-btn {
    max-width: 48px;
    min-width: 48px;
    padding: 0;
}

.mod-detail-stats {
    background: #1d1d37;
    border-radius: 12px;
    padding: 16px 12px;
    display: flex;
    flex-direction: column;
    gap: 8px;
    font-size: 1em;
}

.mod-detail-stat-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2px 0;
    border-bottom: 1px solid #23234a;
}

.mod-detail-stat-row:last-child {
    border-bottom: none;
}

.mod-detail-stat-value {
    font-weight: 600;
    color: #eaeaff;
}

.mod-detail-dep-string {
    display: flex;
    align-items: center;
    gap: 6px;
}

.mod-detail-copy-btn {
    background: none;
    border: none;
    color: #b3b8c5;
    cursor: pointer;
    font-size: 1em;
    padding: 0 2px;
}

.mod-detail-dependants a {
    color: #3ee6b6 !important;
    font-weight: 600;
}

.mod-detail-categories {
    background: #23234a;
    border-radius: 12px;
    padding: 14px 12px 10px 12px;
    margin-top: 10px;
}

.mod-detail-categories-title {
    font-size: 1.1em;
    font-weight: bold;
    color: #eaeaff;
    margin-bottom: 8px;
}

.mod-detail-categories-list {
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
}

.mod-detail-category-badge {
    background: #2e2e57;
    color: #b3b8c5;
    border-radius: 8px;
    padding: 4px 12px;
    font-size: 0.95em;
    font-weight: 600;
}

.mod-detail-sidebar .snext-btn {
    font-size: 1.1em;
}

.mod-detail-meta {
    background: transparent;
    color: #b3b8c5;
    font-size: 1em;
    margin-top: 12px;
    margin-bottom: 12px;
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.mod-detail-meta b {
    color: #fff;
    font-weight: 600;
}

.skeleton {
    background: linear-gradient(90deg, #23283a 25%, #23283a 37%, #23283a 63%);
    border-radius: 8px;
    animation: skeleton-loading 1.2s infinite linear;
    opacity: 0.7;
}

.skeleton-text {
    margin-bottom: 10px;
}

.skeleton-btn {
    border-radius: 8px;
}

@keyframes skeleton-loading {
    0% {
        background-position: -200px 0;
    }

    100% {
        background-position: calc(200px + 100%) 0;
    }
}

@media (max-width: 1100px) {
    .mod-detail-main {
        flex-direction: column;
    }

    .mod-detail-sidebar {
        margin-left: 0;
        margin-top: 32px;
        max-width: 100%;
        width: 100%;
    }
}