.snext-search-wrapper {
    position: relative;
    margin-bottom: 16px;
}

.snext-search {
    width: 90%;
    padding: 10px 14px;
    border-radius: 8px;
    border: none;
    background: #2e2e57;
    color: #fff;
    font-size: 1rem;
    margin-bottom: 24px;
    outline: none;
}

.snext-search-clear {
    position: absolute;
    right: 12px;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    color: #b3b8c5;
    font-size: 1.3em;
    cursor: pointer;
    padding: 0;
    z-index: 2;
}

.snext-search-chip-row {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 8px;
    margin-left: 2px;
}

.snext-search-chip {
    background: #2e2e57;
    color: #fff;
    border-radius: 18px;
    padding: 4px 14px 4px 10px;
    font-weight: 600;
    font-size: 1em;
    display: flex;
    align-items: center;
    gap: 6px;
}

.snext-search-chip-x {
    background: none;
    border: none;
    color: #b3b8c5;
    font-size: 1.1em;
    margin-left: 4px;
    cursor: pointer;
    padding: 0 2px;
}

.snext-search-clearall {
    background: none;
    border: none;
    color: #b3b8c5;
    font-weight: 600;
    font-size: 1em;
    cursor: pointer;
    margin-left: 8px;
    padding: 0 2px 0 0;
    display: inline;
    padding: 4px 14px 4px 10px;
}

.snext-search-clearall:hover {
    background: #2e2e57;
    border-radius: 18px;
}

.snext-search-clearall-x {
    margin-left: 10px;
    text-decoration: none !important;
}