body {
  margin: 0;
  font-family: 'Segoe UI', Arial, sans-serif;
  background: #181c24;
  color: #fff;
  scrollbar-width: thin;
  scrollbar-color: #48488a #181c24;
}

html,
.SporeNEXT {
  scrollbar-width: thin;
  scrollbar-color: #48488a #181c24;
}

html::-webkit-scrollbar,
.SporeNEXT::-webkit-scrollbar {
  width: 10px;
  background: #181c24;
}

html::-webkit-scrollbar-thumb,
.SporeNEXT::-webkit-scrollbar-thumb {
  background: #48488a;
  border-radius: 8px;
}

html::-webkit-scrollbar-thumb:hover,
.SporeNEXT::-webkit-scrollbar-thumb:hover {
  background: #5a5abf;
}

body,
.SporeNEXT {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}


img {
  -webkit-user-drag: none;
  pointer-events: auto;
}

.SporeNEXT {
  min-height: 100vh;
  background: #111722 url('./assets/wallpaper.png') no-repeat center center fixed;
  background-size: cover;
  position: relative;
  z-index: 0;
}

.SporeNEXT::before {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(16, 16, 34, 0.99);
  z-index: 1;
  pointer-events: none;
}

.SporeNEXT>* {
  position: relative;
  z-index: 2;
}

.snext-navbar {
  width: 100%;
  height: 56px;
  background: #1d1d37;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 32px;
  box-sizing: border-box;
  box-shadow: 0 2px 8px #0002;
  position: sticky;
  top: 0;
  z-index: 10;
}

.snext-navbar-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.snext-logo {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.snext-navbar-link {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
  font-size: 1rem;
  padding: 8px 12px;
  border-radius: 6px;
  transition: background 0.15s;
  cursor: pointer;
}

.snext-navbar-link:hover {
  background: #2e2e57;
}

.snext-caret {
  font-size: 0.8em;
  margin-left: 2px;
}

.snext-navbar-right {
  display: flex;
  align-items: center;
  gap: 16px;
}

.snext-navbar-switch {
  font-size: 0.95em;
  opacity: 0.8;
}

.snext-btn {
  font-weight: 600;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  padding: 8px 18px;
  cursor: pointer;
  transition: background 0.15s, color 0.15s;
  margin-left: 2px;
}

.snext-btn-blue {
  background: #14a0e1;
  color: #181c24;
}

.snext-btn-blue:hover {
  background: rgb(80, 195, 248);
}

.snext-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #23283a;
  border: 2px solid #23283a;
  margin-left: 8px;
}

.snext-main {
  display: flex;
  margin-top: 24px;
  max-width: 1600px;
}

.snext-sidebar {
  position: sticky;
  align-self: flex-start;
  height: fit-content;
  top: 80px;
  width: 220px;
  background: #1d1d37;
  padding: 30px 40px 0 40px;
  margin-left: 20px;
  margin-top: 80px;
  margin-bottom: 30px;
  border-radius: 16px;
  z-index: 2;
}

.snext-section {
  margin-bottom: 32px;
}

.snext-section-title {
  font-size: 1.1em;
  font-weight: bold;
  margin-bottom: 10px;
  color: #b3b8c5;
}

.snext-section-item {
  padding: 7px 0 7px 12px;
  border-radius: 6px;
  color: #b3b8c5;
  cursor: pointer;
  font-size: 1em;
  margin-bottom: 2px;
  transition: background 0.15s, color 0.15s;
}

.snext-section-item.active,
.snext-section-item:hover {
  background: #2e2e57;
}

.snext-content {
  flex: 1 1;
  padding: 30px 32px 32px 60px;
}

.snext-content-header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 24px;
  margin-bottom: 18px;
  font-size: 1.1em;
  color: #b3b8c5;
}

.snext-sort-btn {
  background: #23283a;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 7px 16px;
  font-size: 0.9em;
  cursor: pointer;
  transition: background 0.15s;
}

.snext-sort-btn:hover {
  background: #35357a;
}

.snext-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 10px;
  min-height: 800px;
}

.snext-card {
  background: #1d1d37;
  border-radius: 16px;
  padding: 18px;
  box-shadow: 0 2px 8px #0002;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 500px;
  min-height: 500px;
  max-height: 500px;
  width: 200px;
  min-width: 200px;
  max-width: 200px;
  transition: background 0.3s cubic-bezier(0.4, 0, 0.2, 1) 0s;
}

.snext-card-bottom {
  margin-top: auto;
}


.snext-card:hover {
  background: #242444;
}

.snext-card-img {
  width: 100%;
  height: 120px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3.5em;
  font-weight: bold;
  margin-bottom: 16px;
  color: #fff;
}

.snext-card-img img {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  display: block;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: none;
}

.snext-card-img-r {
  background: #2e3a4a;
}

.snext-card-img-bepinex {
  background: #3a2e2e;
}

.snext-card-title {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 4px;
  color: #fff;
}

.snext-card-author {
  font-size: 0.95em;
  color: #14a0e1;
  margin-bottom: 8px;
}

.snext-card-author span {
  color: #14a0e1;
}

.snext-card-desc {
  font-size: .875rem;
  color: #b3b8c5;
}

.snext-card-tags {
  margin-bottom: 0px;
}

.snext-tag {
  display: inline-block;
  background: #181c24;
  color: #14a0e1;
  font-size: 0.85em;
  padding: 3px 10px;
  border-radius: 6px;
  margin-top: 10px;
  margin-right: 6px;
  font-weight: 600;
}

.snext-card-row {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0;
  margin-bottom: 2px;
}

.snext-card-meta {
  font-size: 1em;
  color: #b3b8c5;
  display: flex;
  gap: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  align-items: center;
}

.snext-card-meta span {
  display: flex;
  align-items: center;
  gap: 4px;
}

.snext-card-meta span .svg-inline--fa {
  transition: transform 0.2s;
}

.snext-card-meta span:hover .svg-inline--fa {
  transform: scale(1.3);
}

.snext-card-timestamp {
  font-size: 0.9em;
  color: #b3b8c5;
  display: flex;
  gap: 16px;
  margin: 0;
  padding: 0;
  align-items: center;
}

.snext-sort-menu {
  position: absolute;
  top: 110%;
  right: 0;
  background: #23234a;
  border-radius: 12px;
  box-shadow: 0 4px 24px #0006;
  min-width: 210px;
  z-index: 20;
  padding: 8px 0;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.snext-sort-menu-item {
  color: #fff;
  font-size: 0.9em;
  padding: 12px 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 12px;
  border: none;
  background: none;
  transition: background 0.13s, color 0.13s;
}

.snext-sort-menu-item:hover,
.snext-sort-menu-item.active {
  background: #35357a;
}

.snext-sort-menu-icon {
  font-size: 1.2em;
  width: 24px;
  display: inline-block;
  text-align: center;
}

.snext-logout-btn {
  color: #fff;
  transition: background 0.15s, color 0.15s;
}

.snext-logout-btn:hover {
  color: #14a0e1;
}

.snext-logout-btn:hover .svg-inline--fa {
  color: #14a0e1;
}